import { createRouter, createWebHistory } from "vue-router";

const routes = [
    {
        path: '/',
        name: 'Home',
        component: () => import('@/views/Home.vue'),
        meta: {
            name: 'Главная',
            transition: "back-in",
        },
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import('@/views/Login.vue'),
        meta: {
            name: 'Авторизация',
            transition: "back-in",
        },
    },
    {
        path: '/registration',
        name: 'Registration',
        component: () => import('@/views/Registration.vue'),
        meta: {
            name: 'Регистрация',
            transition: "back-in",
        },
    },
    {
        path: '/abilityList',
        name: 'AbilityList',
        component: () => import('@/views/AbilityList.vue'),
        meta: {
            name: 'Список навыков',
            transition: "back-in",
        },
    },
    {
        path: '/characterList',
        name: 'CharacterList',
        component: () => import('@/views/CharacterList.vue'),
        meta: {
            name: 'Список персонажей',
            transition: "back-in",
        },
    },
    {
        path: "/error",
        name: "Error",
        component: () => import("@/views/ErrorPage.vue"),
        meta: {
            name: '404',
            transition: "back-in",
        },
    },
    {
        path: "/:pathMatch(.*)*",
        redirect: "/error",
        meta: {
            name: 'Redirect',
        },
    },
]

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;
