<template>
  <Menubar :model="items" style="width: 100%;">
    <template #start>
      <img alt="logo" src="https://www.primefaces.org/wp-content/uploads/2020/05/placeholder.png" height="40"
           class="mr-2">
    </template>
    <template #end>
      <!--        <Button icon="pi pi-user" class="p-button-rounded p-button-info" @click="$router.push({name: 'Login'})" />-->

      <SpeedDial :model="itemsNoAuth"
                 direction="down"
                 :transitionDelay="80"
                 showIcon="pi pi-bars"
                 hideIcon="pi pi-times"
                 buttonClass="login__btn"
      />
    </template>
  </Menubar>
</template>

<script>
import Menubar from 'primevue/menubar'
import Button from 'primevue/button'
import SpeedDial from 'primevue/speeddial';

export default {
  name: "Header",
  components: {
    Menubar,
    Button,
    SpeedDial,
  },
  data() {
    return {
      visibleLeft: false,
      items: [
        {
          label: 'Главная',
          icon: 'pi pi-fw pi-home',
          filter: true,
          to: '/'
        },
        {
          label: 'Список навыков',
          icon: 'pi pi-fw pi-pencil',
          to: '/abilityList'
        },
        {
          label: 'Список персонажей',
          icon: 'pi pi-fw pi-user',
          to: '/characterList'
        },
      ],
      itemsNoAuth: [
        {
          label: 'Add',
          icon: 'pi pi-sign-in',
          command: () => {
            this.$router.push('login');
          }
        },
        {
          label: 'Update',
          icon: 'pi pi-search',
          command: () => {
            this.$router.push('registration');
          }
        },
      ]
    }
  },
  computed: {
    navigator() {
      return this.$router.options.routes.filter(item => item.meta.navigator)
    }
  },
}
</script>

<style lang="scss" >
.login {

  &__btn {
    width: 3rem;
    height: 3rem;
  }
}

@media (max-width: 900px) {
  .p-speeddial-button.p-button.p-button-icon-only {
    width: 2rem;
    height: 2rem;
  }
  .p-speeddial-button.p-button.p-button-icon-only .p-button-icon {
    font-size: 1rem;
  }
  .p-speeddial-action {
    width: 2rem;
    height: 2rem;
    background: #ff4081;
    color: #fff;
  }
  .p-speeddial-direction-down {
    right: 10px;
    top: 20px;
  }
}
@media (min-width: 901px) {
  .p-speeddial-button.p-button.p-button-icon-only {
    width: 3rem;
    height: 3rem;
  }
  .p-speeddial-direction-down {
    right: 10px;
    top: 10px;
  }
}

.header {
  background-color: green;
  height: 15%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;

  &__item {
    border: 1px solid;
    cursor: pointer;
    margin: 10px;
  }
}
</style>
