      <component v-if="true" :is="Component" />
      <Preloader v-else overlay-mode />
    </transition>
  </router-view>
  </Layout>
</template>

<script>
import "animate.css";
import Preloader from "./components/ui/Preloader";
import Layout from "./components/Layout";

export default {
  name: "App",
  components: {Layout, Preloader}
}
</script>

